import Vue from 'vue'
import VueMaterial from 'vue-material'
import 'vue-material/dist/components/mdCore/index.css'
import 'vue-material/dist/components/mdCard/index.css'
import 'vue-material/dist/components/mdIcon/index.css'
import 'vue-material/dist/components/mdButton/index.css'
import 'vue-material/dist/components/mdToolbar/index.css'

Vue.use(VueMaterial.mdCore)
Vue.use(VueMaterial.mdCard)
Vue.use(VueMaterial.mdIcon)
Vue.use(VueMaterial.mdButton)
Vue.use(VueMaterial.mdToolbar)

import AwesomeSwiper from 'components/vue-awesome-swiper'
import VueCodemirror from 'vue-codemirror'
Vue.use(AwesomeSwiper)
Vue.use(VueCodemirror)

Vue.material.theme.registerAll({
  blue: {
    primary: 'blue',
    accent: 'pink'
  }
});

// import App from 'projects/vue-awesome-swiper/App.vue'
// import App from 'projects/vue-clappr-player/App.vue'
import App from 'projects/vue-codemirror/App.vue'
// import App from 'projects/vue-touch-ripple/App.vue'
// import App from 'projects/vue-video-player/App.vue'

import 'sass/app.scss'

new Vue({
  el: 'app',
  template: '<App/>',
  components: { App }
})



